nav{
    min-height: 10vh;
    display: flex;
    justify-content: space-around;
    align-items: center;

    button{
        background: white;
        border: none;
        cursor: pointer;
        border: 2px solid rgb(65, 65, 65);
        padding: .5rem 2rem;
        transition: all .3s ease;
        &:hover{
            background: rgb(65, 65, 65);
            color: white;
        }
    }
}


@media screen and (max-width: 768px) {
    nav{
        button{
            z-index: 999;
        }
    }
}